<template>
  <div class="portfolio-detail">
    <div class="page-container">
      <!-- 返回按钮 -->
      <div class="back-section">
        <t-button theme="default" variant="text" @click="goBack">
          <template #icon>
            <t-icon name="chevron-left" />
          </template>
          返回作品集
        </t-button>
      </div>

      <!-- 作品详情 -->
      <div v-if="work" class="work-detail">
        <div class="work-header">
          <div class="work-image-gallery">
            <div 
              v-for="(image, index) in getCategoryImages(work.category)" 
              :key="index"
              class="gallery-item"
              :class="{ active: currentImageIndex === index }"
              @click="setCurrentImage(index)"
            >
              <img 
                :src="image.thumbnail"
                :alt="`${work.title} - 图片${index + 1}`"
                class="gallery-thumbnail"
              />
            </div>
          </div>
          
          <div class="work-image-container">
            <img 
              v-if="currentImage"
              :src="currentImage.full"
              :alt="work.title"
              class="work-image"
            />
            <div v-else class="image-placeholder">
              <t-icon name="image" size="60px" />
            </div>
          </div>
          
          <div class="work-info">
            <h1 class="work-title">{{ work.title }}</h1>
            <p class="work-description">{{ work.description }}</p>
            
            <div class="work-meta">
              <div class="meta-item">
                <span class="meta-label">分类：</span>
                <span class="meta-value">{{ getCategoryName(work.category) }}</span>
              </div>
              <div class="meta-item">
                <span class="meta-label">标签：</span>
                <div class="tags-container">
                  <t-tag v-for="tag in work.tags" :key="tag" theme="default" variant="light">
                    {{ tag }}
                  </t-tag>
                </div>
              </div>
              <div class="meta-item">
                <span class="meta-label">创建时间：</span>
                <span class="meta-value">{{ formatDate(work.createdAt) }}</span>
              </div>
              <div class="meta-item">
                <span class="meta-label">状态：</span>
                <t-tag :theme="work.isPublished ? 'success' : 'warning'" variant="light">
                  {{ work.isPublished ? '已发布' : '草稿' }}
                </t-tag>
              </div>
            </div>

            <div class="work-actions">
              <t-button theme="primary" size="large" @click="viewPortfolio">
                浏览更多作品
              </t-button>
              <t-button theme="default" size="large" @click="shareWork">
                分享作品
              </t-button>
            </div>
          </div>
        </div>

        <!-- 作品详情内容 -->
        <div class="work-content">
          <section class="content-section">
            <h2>作品介绍</h2>
            <p>{{ getDetailedDescription(work) }}</p>
          </section>

          <section class="content-section">
            <h2>创作背景</h2>
            <p>{{ getCreationBackground(work) }}</p>
          </section>

          <section class="content-section">
            <h2>技术说明</h2>
            <p>{{ getTechnicalDetails(work) }}</p>
          </section>
        </div>

        <!-- 相关作品推荐 -->
        <div class="related-works">
          <h2 class="section-title">相关作品</h2>
          <div class="related-grid">
            <div 
              v-for="relatedWork in relatedWorks" 
              :key="relatedWork.id" 
              class="related-item"
              @click="viewWork(relatedWork.id)"
            >
              <div class="related-image">
                <img :src="getRelatedWorkImage(relatedWork)" :alt="relatedWork.title" />
              </div>
              <div class="related-info">
                <h4>{{ relatedWork.title }}</h4>
                <p>{{ relatedWork.description }}</p>
                <span class="related-category">{{ getCategoryName(relatedWork.category) }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!-- 未找到作品 -->
      <div v-else class="not-found">
        <t-icon name="error-circle" size="80px" color="var(--td-error-color, #e34d59)" />
        <h2>作品不存在</h2>
        <p>抱歉，您访问的作品不存在或已被删除。</p>
        <t-button theme="primary" @click="viewPortfolio">返回作品集</t-button>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, onMounted } from 'vue'
import { useRouter, useRoute } from 'vue-router'
import { useWorksStore } from '@/stores/works'
import { imageConfig } from '@/data/images'

const router = useRouter()
const route = useRoute()
const worksStore = useWorksStore()

// 作品数据 - 从 store 中读取
const works = computed(() => worksStore.publishedWorks)

// 当前选中的图片索引
const currentImageIndex = ref(0)

// 当前作品
const work = computed(() => {
  const id = route.params.id as string
  return works.value.find(w => w.id === id)
})

// 获取当前作品的图片
const getCategoryImages = (category: string) => {
  // 优先使用当前作品的图片
  if (work.value) {
    const images = []
    if (work.value.thumbnail) {
      images.push({
        thumbnail: work.value.thumbnail,
        full: work.value.fullImage || work.value.thumbnail
      })
    }
    // 如果当前作品有图片，直接返回
    if (images.length > 0) {
      return images
    }
  }
  
  // 如果当前作品没有图片，从 imageConfig 获取该分类的默认图片
  const configImages = imageConfig.works[category as keyof typeof imageConfig.works] || []
  return configImages
}

// 当前显示的图片
const currentImage = computed(() => {
  if (!work.value) return null
  const images = getCategoryImages(work.value.category)
  return images[currentImageIndex.value] || images[0]
})

// 相关作品（排除当前作品）
const relatedWorks = computed(() => {
  if (!work.value) return []
  return works.value.filter(w => w.id !== work.value?.id && w.category === work.value?.category).slice(0, 3)
})

// 设置当前图片
const setCurrentImage = (index: number) => {
  currentImageIndex.value = index
}

// 获取相关作品图片
const getRelatedWorkImage = (relatedWork: any) => {
  // 优先使用作品的缩略图
  if (relatedWork.thumbnail) {
    return relatedWork.thumbnail
  }
  
  // 如果没有缩略图，从 imageConfig 获取
  if (relatedWork.category) {
    const configImages = imageConfig.works[relatedWork.category as keyof typeof imageConfig.works]
    if (configImages && configImages.length > 0) {
      return configImages[0].thumbnail
    }
  }
  
  // 使用占位图
  return 'https://picsum.photos/seed/related/300/200.jpg'
}

// 获取分类名称
const getCategoryName = (category: string) => {
  const categoryMap: Record<string, string> = {
    painting: '绘画',
    photography: '摄影',
    digital: '数字艺术',
    cityscape: '城市印象'
  }
  return categoryMap[category] || category
}

// 获取详细描述
const getDetailedDescription = (work: any) => {
  if (!work) return ''
  
  const descriptions: Record<string, string> = {
    photography: '这组摄影作品记录了我对光影变化的敏锐观察，从清晨的第一缕阳光到黄昏的最后一抹余晖，每一个瞬间都诉说着独特的故事。',
    painting: '通过水彩画笔，我将内心的情感与观察转化为色彩的语言，希望观者能够感受到画面背后的温度与情感。',
    digital: '在数字艺术的创作中，我探索了算法美与人类创造力的边界，用代码构建出既理性又富有诗意的视觉体验。',
    cityscape: '城市摄影系列是我对现代都市文化的深度观察，通过镜头记录下城市的呼吸、节奏与温度。'
  }
  
  return descriptions[work.category] || work.description
}

// 获取创作背景
const getCreationBackground = (work: any) => {
  if (!work) return ''
  
  const backgrounds: Record<string, string> = {
    photography: '这个系列开始于疫情期间，当时我每天都会在固定的时间走出家门，用相机记录下城市在特殊时期呈现出的别样宁静与生机。',
    painting: '水彩画的创作源于我对自然色彩的迷恋，每一次调色都是与色彩材料之间的对话过程，充满了不确定性与惊喜。',
    digital: '数字艺术的学习始于我对编程的好奇，逐渐发现算法可以创造出传统媒介难以实现的视觉效果，这让我着迷于技术与艺术的结合。',
    cityscape: '在旅行过程中，我特别喜欢观察不同城市的建筑风格与人文特色，每个城市都有其独特的视觉语言和节奏。'
  }
  
  return backgrounds[work.category] || '创作灵感来源于对日常生活的细致观察与感悟。'
}

// 获取技术细节
const getTechnicalDetails = (work: any) => {
  if (!work) return ''
  
  const technical: Record<string, string> = {
    photography: '使用 Canon EOS R5 相机，主要搭配 24-70mm f/2.8 镜头拍摄。注重自然光的运用，后期处理保持低饱和度，强调光影的原始美感。',
    painting: '主要使用温莎·牛顿水彩颜料，在阿诗水彩纸上创作。采用湿画法与干画法结合，通过水分控制色彩的流动与融合。',
    digital: '使用 Processing 和 p5.js 进行创意编程，结合 Three.js 制作三维效果。注重算法的生成性与交互性的结合，探索参数化设计的美学。'
  }
  
  return technical[work.category] || '创作过程中不断尝试新的技术与表现手法，希望能够在传统与创新之间找到平衡。'
}

// 格式化日期
const formatDate = (dateString: string) => {
  const date = new Date(dateString)
  return date.toLocaleDateString('zh-CN', {
    year: 'numeric',
    month: 'long',
    day: 'numeric'
  })
}

// 返回作品集
const goBack = () => {
  router.push('/portfolio')
}

// 查看作品集
const viewPortfolio = () => {
  router.push('/portfolio')
}

// 查看作品
const viewWork = (id: string) => {
  router.push(`/portfolio/${id}`)
}

// 分享作品
const shareWork = () => {
  if (navigator.share) {
    navigator.share({
      title: work.value?.title,
      text: work.value?.description,
      url: window.location.href
    })
  } else {
    // 复制链接到剪贴板
    navigator.clipboard.writeText(window.location.href)
    alert('链接已复制到剪贴板')
  }
}

// 初始化
onMounted(() => {
  worksStore.initWorks()
})
</script>

<style lang="scss" scoped>
@import "@/styles/variables.scss";

.portfolio-detail {
  min-height: 100vh;
  background: $background-color;
}

.back-section {
  padding: $spacing-lg 0;
}

.work-detail {
  .work-header {
    display: grid;
    grid-template-columns: 300px 1fr;
    gap: $spacing-xl;
    margin-bottom: $spacing-xxl;
    
    @media (max-width: $breakpoint-lg) {
      grid-template-columns: 1fr;
    }
  }
}

.work-image-gallery {
  display: flex;
  flex-direction: column;
  gap: $spacing-sm;
  
  .gallery-item {
    border-radius: 8px;
    overflow: hidden;
    cursor: pointer;
    transition: all $transition-normal ease;
    
    &:hover {
      transform: scale(1.05);
    }
    
    &.active {
      box-shadow: 0 0 0 2px $primary-color;
    }
    
    .gallery-thumbnail {
      width: 100%;
      height: 60px;
      object-fit: cover;
    }
  }
}

.work-image-container {
  position: relative;
  aspect-ratio: 4/3;
  border-radius: 12px;
  overflow: hidden;
  background: $gray-light;
  
  .work-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .image-placeholder {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: $text-tertiary;
  }
}

.work-info {
  padding: $spacing-lg 0;
  
  .work-title {
    font-size: 2.2rem;
    font-weight: 700;
    color: $text-primary;
    margin-bottom: $spacing-md;
  }
  
  .work-description {
    font-size: 1.1rem;
    color: $text-secondary;
    line-height: 1.6;
    margin-bottom: $spacing-lg;
  }
  
  .work-meta {
    display: flex;
    flex-direction: column;
    gap: $spacing-sm;
    margin-bottom: $spacing-xl;
    
    .meta-item {
      display: flex;
      align-items: center;
      gap: $spacing-xs;
      
      .meta-label {
        font-size: 0.9rem;
        color: $text-secondary;
      }
      
      .meta-value {
        font-size: 0.9rem;
        color: $text-primary;
        font-weight: 500;
      }
      
      .tags-container {
        display: flex;
        gap: $spacing-xs;
        flex-wrap: wrap;
      }
    }
  }
  
  .work-actions {
    display: flex;
    gap: $spacing-md;
  }
}

.work-content {
  margin-bottom: $spacing-xxl;
  
  .content-section {
    margin-bottom: $spacing-xl;
    
    h2 {
      font-size: 1.5rem;
      font-weight: 600;
      color: $primary-color;
      margin-bottom: $spacing-md;
    }
    
    p {
      font-size: 1.05rem;
      color: $text-secondary;
      line-height: 1.7;
    }
  }
}

.related-works {
  .section-title {
    font-size: 1.5rem;
    font-weight: 600;
    color: $primary-color;
    margin-bottom: $spacing-lg;
  }
  
  .related-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: $spacing-lg;
    
    @media (max-width: $breakpoint-md) {
      grid-template-columns: 1fr;
    }
  }
  
  .related-item {
    display: flex;
    gap: $spacing-md;
    padding: $spacing-md;
    border: 1px solid $border-color;
    border-radius: 12px;
    cursor: pointer;
    transition: all $transition-normal ease;
    
    &:hover {
      box-shadow: $shadow-md;
      transform: translateY(-2px);
    }
  }
  
  .related-image {
    width: 80px;
    height: 60px;
    border-radius: 8px;
    overflow: hidden;
    flex-shrink: 0;
    
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  
  .related-info {
    flex: 1;
    
    h4 {
      font-size: 1.1rem;
      font-weight: 600;
      color: $text-primary;
      margin-bottom: $spacing-xs;
    }
    
    p {
      font-size: 0.9rem;
      color: $text-secondary;
      line-height: 1.4;
      margin-bottom: $spacing-sm;
    }
    
    .related-category {
      font-size: 0.8rem;
      color: $primary-color;
      background: mix($primary-color, $background-color, 10%);
      padding: 4px 8px;
      border-radius: 12px;
    }
  }
}

.not-found {
  text-align: center;
  padding: $spacing-xxl 0;
  
  h2 {
    font-size: 1.8rem;
    color: $text-primary;
    margin-bottom: $spacing-md;
  }
  
  p {
    color: $text-secondary;
    margin-bottom: $spacing-xl;
    line-height: 1.6;
  }
}

@media (max-width: $breakpoint-lg) {
  .work-detail {
    .work-header {
      gap: $spacing-lg;
    }
  }
  
  .work-content {
    margin-bottom: $spacing-lg;
  }
}

@media (max-width: $breakpoint-md) {
  .back-section {
    padding: $spacing-md 0;
  }
  
  .work-info {
    padding: $spacing-md 0;
    
    .work-actions {
      flex-direction: column;
    }
  }
  
  .work-content {
    .content-section {
      margin-bottom: $spacing-lg;
    }
  }
}
</style>